.statsContainer {
	padding-top: 70px;
}

.statsCard {
	box-shadow: 5px 10px 18px 1px #0d1c05;
	background:linear-gradient(135deg, #98db63 0%,#6fca74 100%);
	border-radius: 15px;
	border-color: transparent;
}

.squareItem {
	margin-top: 30px;
    position: relative;
}

.squareInnerContainer {
	padding: 50px 15px 35px;
	text-align: center;
}

.squareInnerContainer h2 {
	font-size: 5rem;
	margin-bottom: 25px;
	visibility: hidden;
	font-weight: 600;
	text-shadow: 2px 5px 8px rgba(0, 0, 0, 1);
	color: #eb3548;
}

.animateValue {
	-webkit-animation-name: valueAppear;
	        animation-name: valueAppear;
	-webkit-animation-duration: 0.4s;
	        animation-duration: 0.4s;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
}

@-webkit-keyframes valueAppear {
	from { -webkit-transform: scale(0); transform: scale(0); visibility: hidden;}
	to { -webkit-transform: scale(1); transform: scale(1); visibility: visible;}
}

@keyframes valueAppear {
	from { -webkit-transform: scale(0); transform: scale(0); visibility: hidden;}
	to { -webkit-transform: scale(1); transform: scale(1); visibility: visible;}
}

.squareInnerContainer h3 {
	font-size: 1.2rem;
	font-weight: 700;
	text-shadow: 1px 8px 4px rgba(0, 0, 0, 0.2);
    margin: 0;
	color: #eb3548;
}

.squareIcon {
	position: absolute;
	color: white;
	background-color: #eb3548;
	right: 40%;
	font-size: 70px;
	border-radius: 50%;
	padding: 3px;
	box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.4);
	cursor: pointer;
}

.animateIcon {
	-webkit-animation-name: iconMove;
	        animation-name: iconMove;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
	-webkit-animation-duration: 0.2s;
	        animation-duration: 0.2s;
	cursor: default;
}

@-webkit-keyframes iconMove {
	from{ -webkit-transform: translate(0px, 0px); transform: translate(0px, 0px); }
	to{ 
		-webkit-transform: translate(-150%, -90%) scale(0.55); 
		        transform: translate(-150%, -90%) scale(0.55);
	}
}

@keyframes iconMove {
	from{ -webkit-transform: translate(0px, 0px); transform: translate(0px, 0px); }
	to{ 
		-webkit-transform: translate(-150%, -90%) scale(0.55); 
		        transform: translate(-150%, -90%) scale(0.55);
	}
}

